html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
  background-color: $c-light;
  color: $c-dark;
  font-size: 16px;
  min-height: 100%;
  max-width: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: $c-dark;
}

body {
  min-height: 100%;
  max-width: 100%;
  overflow-x: hidden;
}

body.dark-overlay {
  &:after {
    content: '';
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    min-height: 100%;
    z-index: 97;
    background: rgba(0, 0, 0, 0.4);
  }
}

main {
  min-height: 100%;
}

.section{
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
  position: relative;
}

.title{
  text-align: center;
  margin-bottom: 2.5rem;
  & h2{
    margin-top: 0;
  }
}

.top-right-corner{
  position: absolute;
  width: 60px;
  height: 60px;
  background: linear-gradient(44.84deg, #368BE3 50.1%, $c-light 50.33%);
  box-shadow: -2px 2px 16px rgba(0, 0, 0, 0.25);
  top: 0;
  right: 0;
}

footer{
  padding-bottom: 20rem;
}