.section-heading{
  min-height: 100vh;
  background-color: $c-alpha;
  color: set-text-color($c-alpha);
  background-image: url('../img/bg.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 0;
  position: relative;
  padding-top: 0;
  padding-bottom: 0;

  &:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: linear-gradient(180deg, rgba(40, 71, 115, 0.88) 37.21%, #284773 99.21%);
  }
}

.section-heading__content{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  text-align: center;
}